@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Roboto', sans-serif;
}

.App {
  position: relative;
  background-image: url('../img/header.jpg');
  min-height: 100vh;
  overflow-x: hidden;
}

/* .header {
  flex-basis: 35vh;
  position: relative;
  background-color: #aaa;
  overflow: hidden;
} */

.menu {
  position: absolute;
  right: -200px;
  top: 20px;
  width: 200px;
  background-color: rgba(255, 255, 255, .2);
  border-radius: 20px;
  transition: .3s;
}

.menu.active {
  right: 20px;
}

.menuButton {
  position: absolute;
  left: -50px;
  top: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .2);
  font-size: 26px;
  line-height: 40px;
  text-align: center;
  color: #fff;
}

.main {
  position: absolute;
  left: 20px;
  right: 60px;
  top: 20px;
  bottom: calc(5vh + 20px);
  padding: 20px;
  background-color: rgba(255, 255, 255, .2);
  border-radius: 20px;
  overflow: hidden;
  transition: .3s;
}

.main.active {
  right: 280px;
}

.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 5vh;
  text-align: center;
  font-size: 20px;
  line-height: 5vh;
  background-color: #000;
  color: #fff;
}